.footer {
  width: 100%;
  height: 200px;
  background-color: #121619;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 10px;
}

.socialMedia svg {
  color: white;
  margin: 20px;
  font-size: 70px;
  cursor: pointer;
  margin-bottom: 10px;
}


.footer p {
  color: white;
}
@media only screen and (max-width: 650px) {
  .footer p {
    color: white;
    margin-top: -15px;
  }
  .socialMedia svg {
    margin-left: 52px;
  }
}